  li {
    list-style: none;
    float: left;
    width: auto;
    height: 65px;
    overflow: hidden;
    margin: 0px 0px 0px 0px;
    padding: 0px 0px 0px 0px;

    a {
      list-style: none;
      float: left;
      width: auto;
      height: 65px;
      overflow: hidden;
      margin: 0px 0px 0px 0px;
      padding: 20px 15px 20px 15px;
      border: 0px;
      font-size: 15px;
      font-weight: 600;
      text-decoration: none;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      -ms-box-sizing: border-box;
      box-sizing: border-box;

      i {
        float: left;
        width: auto;
        height: auto;
        overflow: hidden;
        margin: 0px 8px 0px 0px;
        padding: 3px 2px 2px 2px;
        border: 0px;
        font-size: 18px;
        font-weight: normal;
        font-style: normal;
        text-decoration: none;
      }
    }

    &:hover a {
      text-decoration: none;
    }

    ul {
      display: block;
      border: 0px;
      float: left;
      width: 230px;
      z-index: 9999;
      position: absolute;
      left: -999em;
      height: auto;
      margin: 60px 0px 0px 0px;
      padding: 6px 15px 15px 15px;
      border-radius: 0px 0px 10px 10px;
      clear: both;

      li ul {
        display: block;
        border: 0px;
        float: left;
        width: 190px;
        z-index: 9999;
        position: absolute;
        left: -999em;
        height: auto;
        margin: -6px 0px 0px 230px;
        padding: 6px 15px 15px 15px;
        border-radius: 0px 10px 10px 10px;
        clear: both;
      }
    }
  }
}

.ustmenu li {
  ul li {
    list-style: none;
    background: none;
    float: left;
    width: 100%;
    height: auto;
    overflow: hidden;
    margin: 0px 0px 0px 0px;
    padding: 0px 0px 0px 0px;
    border: 0px;

    &:hover {
      list-style: none;
      background: none;
      float: left;
      width: 100%;
      height: auto;
      overflow: hidden;
      margin: 0px 0px 0px 0px;
      padding: 0px 0px 0px 0px;
      border: 0px;
    }

    i, &:hover i {
      display: none !important;
    }
  }

  li a {
    background: none;
    float: left;
    width: 100%;
    height: auto;
    overflow: hidden;
    margin: 0px 0px 0px 0px;
    padding: 12px 0px 12px 0px;
    border: 0px;
    font-size: 14px;
    font-weight: 600;
    text-decoration: none;

    &:link, &:visited {
      background: none;
      float: left;
      width: 100%;
      height: auto;
      overflow: hidden;
      margin: 0px 0px 0px 0px;
      padding: 12px 0px 12px 0px;
      border: 0px;
      font-size: 14px;
      font-weight: 600;
      text-decoration: none;
    }

    &:hover {
      background: none;
      text-decoration: underline;
    }
  }

  ul ul {
    margin: -32px 0 0 171px;
  }

  &:hover ul ul, &.sfhover ul ul {
    left: -999em;

    ul {
      left: -999em;
    }
  }

  &:hover ul {
    left: auto;
  }

  li {
    &:hover ul, li:hover ul {
      left: auto;
    }
  }

  &.sfhover ul {
    left: auto;
  }

  li {
    &.sfhover ul, li.sfhover ul {
      left: auto;
    }
  }

  &:hover, &.sfhover {
    position: static;
  }
}

