// Styleguide Utilities.Flex.Direction

//
// Wrapping
//
// | Class                          | Description |
// | ------------------------------ | ----------- |
// | `.u-flex-wrap-no-wrap`         | Don't allow flex items to wrap. |
// | `.u-flex-wrap-wrap`            | Allow flex items to wrap in the normal direction. |
